body {
  margin: 0;
}

main {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50vmin;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes {
  z-index: 1;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite watch-start;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch__seconds {
  z-index: 2;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite watch-start;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch__pivot {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50vmin;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up > .stopwatch__minutes {
  animation-duration: 600s;
}

@keyframes watch-start {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
